.main {
  height: 100vh;
  padding-top: 95px;
  box-sizing: border-box;
  display: grid;
  grid-template-rows: 2fr auto 1fr;
} 

.section-top {
  display: grid;
  justify-items: center;
  align-items: center;
}

.section-mid {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  font-size: 1.5em;
  font-weight: 500;
  color: rgb(47,185,109);
}

.thumb {
  width: 20vw;
  border-radius: 1vw;
  box-shadow: 0 0 10px 1px rgba(171,240,194, .8);
}

.section-bottom {
  bottom: 0;
  right: 0;
  left: 0;
  padding: 20px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  gap: 10px;
}

input[type="range"] {
  -webkit-appearance: none;
  width: 85%;
  outline: none;
  height: 7px;
  border-radius: 5px;
  margin: 0 15px;
  background-color: rgb(171, 240, 194);
  cursor: pointer;
}

input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 20px;
  width: 20px;
  background-color: rgb(96, 197, 135);
  border-radius: 50%;
  cursor: pointer;
}

.duration-section {
  width: 100%;
  display: grid;
  grid-template-columns: 90px auto 90px;
  justify-items: center;
  align-items: center;
  color: white;
  padding: 8px 12px;
  box-sizing: border-box;
}

.countdown {
  background-color: rgb(50,186,108);
  padding: 5px 8px;
  border-radius: 15px;
}

.range-duration {
  width: 100%;
}

input[type="range"] .range-duration {
  background-color: rgb(50,186,108);
}

.range-duration::-webkit-slider-thumb {
  border: 2px solid rgb(255, 251, 251);
}

.durationReci {
  background-color: rgb(50,186,108);
  padding: 5px 8px;
  border-radius: 15px;
}

.controls-section {
  width: 100%;
}

.bar-control {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-content: center;
  box-sizing: border-box;
  padding: 8px 25px;
  width: 100%;
  background-color: rgb(50,186,108);
  border-radius: 40px;
  position: relative;
}

.controls-recitation {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.controls-recitation .btn-autoplay {
  margin-left: 20px;
  border-radius: 5px;
  border: none;
  outline: none;
  padding: 10px 16px;
  background-color: rgb(96, 197, 135);
  color: white;
  cursor: pointer;
  font-size: 1em;
} 

.controls-recitation .btn-autoplay-true {
  background-color: rgb(42, 224, 103);
  margin-left: 20px;
  border-radius: 5px;
  border: none;
  outline: none;
  padding: 10px 16px;
  color: white;
  cursor: pointer;
  font-size: 1em;
}

.button-section {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-items: center;
  align-items: center;
}

.button-section button {
  display: flex;
  justify-content: center;
  padding: 20px 22px;
  background-color: rgb(96, 197, 135);
  color: rgb(255, 255, 255);
  border: none; 
  outline: none;
  border-radius: 50%;
  cursor: pointer;
  font-size: 1.2em;
}

.button-section button:hover,
.controls-recitation button:hover {
  background-color: rgb(47, 168, 93);
}

.button-section button i {
  margin: 0;
  padding: 0;
}

.volume-section {
  display: flex;
  align-items: center;
  padding: 5px;
  gap: 10px;
  color: white;
  justify-self: end;
}

.volume-section button {
  cursor: pointer;
  padding: 10px;
  background-color: rgb(50,186,108);
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 1.2em;
  outline: none;
}

.volume-section button:hover {
  background-color:  rgba(109, 216, 171, 0.8);
}

.bar-volume {
  display: grid;
  justify-items: center;
  align-items: center;
  grid-template-columns: auto 40px;
}

.volume-section p {
  text-align: center;
}

.fas.fa-caret-down {
  display: none;
}

@media screen and (max-width: 992px ){
  .thumb {
    width: 40vw;
  }
}

@media screen and (max-width: 768px) {
  .thumb {
    width: 350px;
  }

  .bar-volume {
    display: none;
  }

  .bar-volume2 {
    display: grid;
    justify-items: center;
    align-items: center;
    grid-template-columns: auto 40px;
    position: absolute;
    background-color:rgb(250, 250, 250);
    color: black;
    border-radius: 10px;
    padding: 4px 8px;
    right: 15px;
    bottom: 10;
  }

  .fas.fa-caret-down {
    display: block;
  }
}

@media screen and (max-width: 576px) {

  .section-bottom {
    padding: 2px;
  }

  .duration-section {
    padding: 0;
  }

  .countdown, 
  .durationReci {
    font-size: .8em;
  }

  .controls-recitation .btn-autoplay-true,
  .controls-recitation .btn-autoplay {
    margin-left: 7px;
    padding: 7px 10px;
    color: white;
    cursor: pointer;
    font-size: .8em;
  }

  .button-section button {
    padding: 12px 14px;
    font-size: .8em;
  } 

  .thumb {
    width: 280px;
  }
}

@media screen and (max-width: 365px) {
  .section-top {
    text-align: center;
  }

  .thumb {
    width: 250px;
  }

  .bar-control {
    padding: 4px 15px;
    gap: 5px;
  }


  .controls-recitation .btn-autoplay-true,
  .controls-recitation .btn-autoplay {
    margin-left: 15px;
    padding: 5px 8px;
  }

  .button-section {
    gap: 5px;
  }

  .button-section button {
    padding: 8px 10px;
    font-size: .7em;
  }

  .volume-section button {
    padding: 8px;
    font-size: .7em;
  }
  
}