.title-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: 'Montserrat', sans-serif;
  margin-top: 100px;
}

.wrapping-title {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1px 25px;
  border-bottom: 1px solid black;
}

.bismillah-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  padding-top: 20px;
}

.bismillah {
  text-align: center;
  /* border: 1px solid black; */
}

.bismillah,
.ayat-text {
  font-size: 2.5em;
}

.ayyat-section {
  margin-top: 50px;
}

.ayat {
  border-bottom: 1px solid #000;
  padding: 10px 20px;
}

.ayat-text {
  display: flex;
  justify-content: space-between;
  text-align: right;
  line-height: 80px;
}

.number-ayyat {
  font-size: .7em;
}

.translate {
  font-size: 1.25em;
}


@media screen and (max-width: 576px ) {

  .bismillah,
  .ayat-text {
    font-size: 1.7em;
  }

  .ayat-text {
    line-height: 40px;
  }

  .translate {
    font-size: 1.05em;
  }
}