@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;1,300;1,400;1,500;1,600&display=swap');

* {
  text-decoration: none;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
}

body {
  position: relative;
}

.NavbarItems {
  background: linear-gradient(90deg, rgb(73, 197, 100) 0%, rgb(47, 185, 109) 100%);
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.05em;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
}

.navbar-logo {
  color: #fff;
  justify-self: start;
  margin-left: 20px;
  cursor: pointer;
  font-weight: 400;
}

.fa-arrow-circle-left {
  font-size: 1em;
  color: white;
  margin-right: 10px;
}

.fa-quran,
.fa-book-reader,
.fa-play-circle,
.fa-play {
  margin-left: 10px;
  font-size: 1em;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(4, auto);
  gap: 20px;
  list-style: none;
  text-align: center;
  width: 70vw;
  justify-content: end;
  z-index: 100;
}

.nav-links {
  color: white;
  text-decoration: none;
  padding: 10px 24px;
}

.nav-links:hover {
  background-color: #6df7b2;
  border-radius: 4px;
  transition: all .3s ease-in;
}

.fa-bars {
  color: white;
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 992px) {

  .nav-menu {
    display: flex;
    flex-direction: column;
    gap: 0;
    width: 200px;
    height: max-content;
    position: absolute;
    top: 80px;
    right: -100%;
    opacity: 1;
    transition: all .7s ease;
    overflow: hidden;
  }

  .nav-menu.active {
    background: rgb(48,185,108);
    right: 0;
    opacity: 1;
    transition: all .7s ease;
    /* z-index: 10; */
  }

  .nav-links {
    text-align: center;
    padding: 30px 0px;
    width: 100%;
    display: table;
  }

  .nav-links:hover,
  .nav-links:active {
    background-color: #4cdf9a;
    border-radius: 0;
  }

  .fa-arrow-circle-left {
    font-size: 1em;
    color: white;
    margin-right: 10px;
  }

  .navbar-logo {
    position: absolute;
    left: 0;
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 25px; 
    right: 25px;
    font-size: 25px;
    cursor: pointer;
  }

  .fa-times {
    color: white; 
    font-size: 30px;
  }

}