.wrapping-card-tokoh {
  padding: 10px 100px;
  margin-top: 100px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 25px;
}

.card-tokoh {
  width: 280px;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 0 0 4px 0px rgba(63, 189, 166, .4); 
  padding: 10px;
  /* position: relative; */
}

.img-tokoh {
  width: 100%;
  margin-bottom: 10px;
}

.text-card {
  text-align: center;
}

@media screen and (max-width: 992px) {
  .wrapping-card-tokoh {
    padding: 50px;
  }
  .card-tokoh {
    width: 210px;
  }
}

@media screen and (max-width: 768px) {
  .card-tokoh {
    width: 300px;
  }
}

@media screen and (max-width: 576px) {
  .wrapping-card-tokoh {
    padding: 10px;
  }
  
  .card-tokoh {
    width: 200px;
  }
}