@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;1,300;1,400;1,500;1,600&display=swap');

.jumbotron {
  padding: 30px 50px;
  margin-top: 80px;
  display: flex;
  font-family: 'Montserrat', sans-serif;
  justify-content: space-between;  
}

.jumbotron-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding: 10px;
}

.jumbotron-info h3 {
  font-size: 2.5em;
  font-weight: normal;
  color: rgb(82, 82, 82);
}

.search-section {
  margin-top: 40px;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  align-items: center;
}

.thumbQari {
  width: 250px;
}

.wrapping-card-murottals {
  padding: 10px 50px;
}

.card-murottals {
  background-color: #fff;
  border-bottom: 1px solid rgb(212, 212, 212);
  padding: 8px 12px;
}

@media screen and (max-width: 860px) {
  .jumbotron {
    flex-wrap: wrap;
    justify-content: center;
    padding: 30px 10px;
  }

  .jumbotron-info {
    order: 2;
  }

  h3 {
    margin-top: 20px;
    text-align: center;
  }

  .search-section {
    margin-top: 16px;
  }

  .thumbQari {
    order: 1;
  }

  .wrapping-card-murottals {
    padding: 0;
  }
}

@media screen and (max-width: 576px) {
  .jumbotron {
    padding: 30px 0px;
  }

  .jumbotron-info h3 {
    font-size: 2em;
  }

  .search-section {
    gap: 5px;
  }

  .wrapping-card-murottals {
    padding: 0;
  }

}