}

.board-search {
  width: 400px;
  height: 65px;
  padding: 10px;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 0 0 4px 0px rgba(63, 189, 166, .4); 
  display: flex;
  flex-direction: column;
  grid-gap: 7px;
  gap: 7px;
  margin-top: 100px;
}

label {
  font-family: 'Montserrat', sans-serif;
  /* font-family: 'Akaya Telivigala', cursive; */
  font-weight: 500;
}

.input-keyword {
  width: 385px;
  padding: 5px;
