.wrapper-card {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  justify-content: center;
  grid-gap: 25px;
  gap: 25px;
  padding: 10px 50px;
}

.card {
  padding: 10px;
  border-radius: 10px;
  background-color: #fff;
  /* background-color: rgb(231,246,240); */
  box-shadow: 0 0 4px 0px rgba(63, 189, 166, .4); 
  display: flex;
  flex-direction: column;
  grid-gap: 7px;
  gap: 7px;
  cursor: pointer;  
  font-family: 'Montserrat', sans-serif;
  transition: .4s all ease-out;
  /* color: #23daa8; */
}

.card:hover {
  box-shadow: 0 0 10px 0px rgba(63, 189, 166, .4); 
}

.flex-center {
  padding: 20px 10px;
}

.not-found {
  display: flex;
  justify-content: center;
}

.wrapping-not-found {
  max-width: 100vh;
}

.quran-img {
  width: 100%;
}

.link {
  text-decoration: none;
  color: black;
}
/* CSS GRID */
/* Responsive for < 992px */
@media screen and (max-width: 992px) {
  .wrapper-card {
    grid-template-columns: repeat(3, 1fr);
  }
}

/* Responsive for Mobile*/
@media screen and (max-width : 768px) {
  .wrapper-card {
    padding: 10px;
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 10px;
    gap: 10px;
  }

  .card {
    box-shadow: 0 0 4px 0px rgba(63, 189, 166, .4); 
  }
}
